import React, { useEffect, useState } from 'react'
import { styled } from 'styled-components';
import { createWorker } from "tesseract.js";

const Data = styled.div`border:1px solid gray; display:flex; flex-direction: column; margin:3px; width:75%; box-shadow: gray 0 2px 4px; height: 100%; overflow-y:auto;`

function OCR({ selectedImage }) {
  const [ocr, setOcr] = useState([]);
  const [imageData, setImageData] = useState(null);
  const worker = createWorker({
    logger: (m) => {
      console.log(m);
    },
  });
  const convertImageToText = async () => {
    if (!imageData) return;
    // console.log("qwerty 2", worker);
    let w = await worker;
    // console.log("qwerty 3", w);
    await w.load();
    await w.loadLanguage("eng");
    await w.initialize("eng");
    const result = await w.recognize(imageData);
    console.log("qwerty 3", result);
    setOcr(result?.data?.words);
  };

  useEffect(() => {
    convertImageToText();
  }, [imageData]);

  useEffect(() => {
    if (selectedImage) {
      handleImageChange(selectedImage)
    }
  }, [selectedImage])

  function handleImageChange(file) {
    if (!file) return;
    const reader = new FileReader();
    reader.onloadend = () => {
      const imageDataUri = reader.result;
      console.log({ imageDataUri });
      setImageData(imageDataUri);
    };
    reader.readAsDataURL(file);
  }
  return (
    <Data>
      <div style={{ maxWidth: "100%",height: "100%", display: "flex", flexWrap: "wrap", alignContent: "flex-start" }}>
        {
          ocr.length ?
            ocr.map(val => {
              return (
                <p style={{ border: "1px solid #ddd", padding: "10px 20px", margin: "0" }}>{val.text}</p>
              )
            }) :
            <div style={{ width: "100%", height: "100%", display: "flex", justifyContent: "center", alignItems: "center" }}>
              Extracted text  from Image will be visible here
            </div>
        }
      </div>
    </Data>
  )
}

export default OCR